import React, { Component, Fragment } from "react";
import { FaRobot } from "react-icons/fa";
import { MdOutlineChangeCircle } from "react-icons/md";
import { TransferRequestInterface } from "../TransferApproval";
import DistrictListItem from "./DistrictListItem";
import Chart from "react-apexcharts";
import { displayNumbers } from "../../../utils/functions";

interface ChartDisplay {
  options: {
    chart: {
      id: string;
    };
    xaxis: {
      categories: string[];
    };
  };
  series: [
    {
      name: string;
      data: number[];
    }
  ];
}

interface RunAlgorithmProps {
  districts_requests: TransferRequestInterface[];
}
interface RunAlgorithmState {
  loading: boolean;
  chartData: ChartDisplay;
  count: number;
}

export class RunAlgorithm extends Component<
  RunAlgorithmProps,
  RunAlgorithmState
> {
  constructor(props: RunAlgorithmProps) {
    super(props);

    this.state = {
      count: 0,
      loading: false,
      chartData: {
        options: {
          chart: {
            id: "basic-bar",
          },
          xaxis: {
            categories: [
              "Nyarugenge",
              "Kicukiro",
              "Gasabo",
              "Muhanga",
              "Kayonza",
              "Nyaruguru",
              "Kamonyi",
              "Musanze",
              "Gicumbi",
              "Rubavu",
            ],
          },
        },
        series: [
          {
            name: "series-1",
            data: [1, 2, 4, 1, 8, 3, 5, 7, 9, 5],
          },
        ],
      },
    };
  }
  componentDidMount = () => {
    setInterval(() => {
      this.state.count < 10000 &&
        this.setState({ count: this.state.count + 1 });
    }, 30);
  };
  render() {
    return (
      <Fragment>
        <div className="h-screen bg-white fixed z-40 left-0 right-0 animate__animated animate__fadeIn">
          <div className="bg-primary-800 text-white fixed top-18 left-0 right-0 w-full z-50">
            <div className="flex flex-row items-center justify-between">
              <div className="px-3 py-2 flex flex-row items-center gap-2">
                <div>
                  <FaRobot className="text-5xl -mt-1 animate__animated animate__flash" />
                </div>
                <div className="text-2xl font-extrabold">Transfer runner</div>
              </div>
              <div className="px-2">
                <div className="bg-green-600 hover:bg-green-800 rounded-md font-bold px-3 py-2 cursor-pointer border border-white">
                  Reload process
                </div>
              </div>
            </div>
          </div>
          {/* body */}
          <div className="bg-black h-full overflow-y-auto text-white pt-12 grid grid-cols-12">
            {/* col 1 */}
            <div className="col-span-3 w-full h-full bg-gray-700 pt-3">
              {this.props.districts_requests.map((item, i) => (
                <DistrictListItem
                  key={i}
                  index={i + 1}
                  district_detail={item}
                />
              ))}
            </div>
            {/* col 2 */}
            <div className="col-span-9 pt-6 pl-3">
              <div className="flex flex-row w-full">
                <div>
                  <div></div>
                  <div className="font-extrabold text-lg text-gray-400">
                    Placement progress (Nyanza)
                  </div>
                  <div className="bg-gray-800 h-52 border border-gray-700 rounded-md p-3 px-4 text-sm text-green-300 overflow-hidden cursor-not-allowed flex items-center justify-center gap-3  animate__animated animate__zoomIn">
                    <div>
                      <div>
                        <FaRobot className="text-9xl text-gray-200 animate__animated animate__flash" />
                      </div>
                      <div className="flex flex-row items-center justify-center text-gray-400">
                        <div className="">
                          <MdOutlineChangeCircle className="text-xl animate-spin" />
                        </div>
                        <div>23 mins remaining</div>
                      </div>
                    </div>

                    <div className="flex flex-col text-xs font-extralight">
                      <span>Initializing process ...</span>
                      <span>Placing process ...</span>
                      <span>Placing process, tasking ...</span>
                      <span>Placing process ...</span>
                      <span>Placing process ...</span>
                      <span>Placing process, waiting ...</span>
                    </div>
                  </div>
                </div>
                <div className="pt-8 pl-3">
                  <div className="flex flex-row items-center gap-3 mb-3">
                    <div className="bg-gray-600 rounded-md p-3 animate__animated animate__zoomIn">
                      <div>Waiting</div>
                      <div className="text-4xl font-extrabold">
                        {displayNumbers(22382 - this.state.count)}
                      </div>
                    </div>
                    <div className="bg-green-800 rounded-md p-3 w-1/2 animate__animated animate__zoomIn">
                      <div>Approved</div>
                      <div className="text-4xl font-extrabold">
                        {displayNumbers(
                          this.state.count + (this.state.count - 1)
                        )}
                      </div>
                    </div>
                    <div className="bg-red-800 rounded-md p-3 w-1/2 animate__animated animate__zoomIn">
                      <div>Rejected</div>
                      <div className="text-4xl font-extrabold">
                        {displayNumbers(this.state.count)}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {/*  */}
              <div className="mr-2">
                <div className="h-4 w-full bg-gray-600 mt-2 rounded-full  animate__animated animate__fadeIn">
                  <div
                    className="bg-yellow-400 h-4 rounded-full"
                    style={{
                      width: "70%",
                    }}
                  ></div>
                </div>
              </div>
              <div className="mt-3">
                <div className="mb-2 font-bold text-lg text-gray-400">
                  Districts Transfer monitoring
                </div>
                <div className="px-3 mr-2 rounded-md bg-gray-800">
                  <Chart
                    options={this.state.chartData.options}
                    series={this.state.chartData.series}
                    type="bar"
                    width="100%"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default RunAlgorithm;
