import React, { Component, Fragment } from "react";
import { MdDateRange, MdOutlineKeyboardBackspace } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  TransferAlgorithmStore,
  FC_Load_All_Transfers,
  Run_Transfer_Algorithm,
} from "../../actions";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import { StoreState } from "../../reducers";
import { DATE } from "../../utils/functions";
import { VscDebugStart } from "react-icons/vsc";
import { TransferRequestDetails } from "./TransferRequestDetails";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import RunAlgorithm from "./RunAlgorithm/RunAlgorithm";
import { FaRobot } from "react-icons/fa";
import Loading from "../../components/Loading/Loading";
import { AiOutlineCheckCircle } from "react-icons/ai";
import Robot2 from "../../assets/robot2.gif";

export interface TransferRequestInterface {
  district_code: string;
  district_name: string;
  total_requested: string;
  total_requested_pending: string;
  total_requested_approved: string;
  total_requested_rejected: string;
}

interface TransferApprovalState {
  loading: boolean;
  viewTransferDetails: TransferRequestInterface | null;
  confirm_run_algorithm: boolean;
  run_algorithm_page: boolean;
  success: string;
}
interface TransferApprovalProps {
  auth: Auth;
  basic: Basic;
  transferAlgorithm: TransferAlgorithmStore;
  FC_Load_All_Transfers: (setLoading: (status: boolean) => void) => void;
  Run_Transfer_Algorithm: (setLoading: (status: boolean) => void) => void;
}

class _TransferApproval extends Component<
  TransferApprovalProps,
  TransferApprovalState
> {
  constructor(props: TransferApprovalProps) {
    super(props);

    this.state = {
      loading: false,
      viewTransferDetails: null,
      confirm_run_algorithm: false,
      run_algorithm_page: false,
      success: "",
    };
  }

  componentDidMount = () => {
    if (this.props.transferAlgorithm.transfers === null) {
      this.props.FC_Load_All_Transfers((status: boolean) =>
        this.setState({ loading: status })
      );
    }
  };
  TotalWaiting = (transfers: TransferRequestInterface[] | null) => {
    if (transfers === null) {
      return 0;
    } else {
      let res: number = 0;
      transfers.map((item) => {
        res = res + parseInt(item.total_requested_pending);
        return res;
      });
      return res;
    }
  };

  // Run the algorithm
  RunAlgorithm = () => {
    this.props.Run_Transfer_Algorithm((status: boolean) => {
      if (status === false) {
        this.setState({ loading: true });
        this.props.FC_Load_All_Transfers((status_two: boolean) => {
          if (status_two === true && status === false) {
            this.setState({
              loading: false,
              success: "12,000 Teachers transfer successfully!",
            });
          }
        });
      }
    });
  };
  render() {
    return this.props.transferAlgorithm.transfers === null ||
      this.state.loading === true ? (
      <DashboardContainer>
        <div className="flex flex-col items-center justify-center p-3">
          <Loading />
          <div className="">Loading, please wait...</div>
        </div>
      </DashboardContainer>
    ) : this.state.run_algorithm_page === true ? (
      <RunAlgorithm
        districts_requests={this.props.transferAlgorithm.transfers}
      />
    ) : this.state.viewTransferDetails === null ? (
      <Fragment>
        <DashboardContainer>
          <div className="flex flex-row items-center gap-4 justify-between">
            <div className="w-max">
              <div className="text-3xl font-extrabold">Transfer approval</div>
              <div className="font-extrabold text-primary-800">
                Academic year:{" "}
                {this.props.basic.academic_calender?.academic_year_name}
              </div>
            </div>
            <div>
              <div className="flex flex-row items-center gap-2">
                <div>
                  <MdDateRange />
                </div>
                <span>Transfer start date: </span>
                <span>
                  {this.props.basic.academic_calender &&
                    DATE(
                      this.props.basic.academic_calender.transfer_request_start
                    )}
                </span>
              </div>
              <div className="flex flex-row items-center gap-2">
                <div>
                  <MdDateRange />
                </div>
                <span>Transfer ending date: </span>
                <span>
                  {this.props.basic.academic_calender &&
                    DATE(
                      this.props.basic.academic_calender.transfer_request_end
                    )}
                </span>
              </div>
            </div>
          </div>
          <div className="border-t border-gray-400 my-5 flex flex-row items-center gap-3 justify-between py-2">
            <div className="text-2xl font-light">Transfer requests</div>
            <div
              onClick={() => this.setState({ confirm_run_algorithm: true })}
              className="bg-accent-900 hover:bg-yellow-900 text-white px-3 py-2 cursor-pointer rounded font-bold flex flex-row items-center gap-2 w-max"
            >
              <div>
                <VscDebugStart className="text-2xl" />
              </div>
              <span>Run transfer algorithm</span>
            </div>
          </div>
          <div>
            <table className="w-full">
              <thead className="bg-blue-100 text-primary-800 text-left">
                <tr>
                  <th className="px-3 py-2">#</th>
                  <th className="px-3 py-2">District code</th>
                  <th className="px-3 py-2">District name</th>
                  <th className="px-3 py-2 text-center">Approved</th>
                  <th className="px-3 py-2 text-center">Rejected</th>
                  <th className="px-3 py-2 text-center">Waiting</th>
                  <th className="px-3 py-2 text-center">Total</th>
                  <th className="px-3 py-2 text-right"></th>
                </tr>
              </thead>
              <tbody>
                {this.props.transferAlgorithm.transfers.length === 0 ? (
                  <tr>
                    <td colSpan={8} className="">
                      <div className="bg-gray-100 rounded-md p-5 text-2xl font-light text-center my-2">
                        No requests found!
                      </div>
                    </td>
                  </tr>
                ) : (
                  this.props.transferAlgorithm.transfers.map((item, i) => (
                    <tr
                      key={i + 1}
                      className={`${
                        i % 2 !== 0 ? "bg-gray-100" : ""
                      } hover:bg-blue-50 group`}
                    >
                      <td className="px-3 py-2">{i + 1}</td>
                      <td className="px-3 py-2">{item.district_code}</td>
                      <td className="px-3 py-2">{item.district_name}</td>
                      <td className="px-3 py-2 text-center">
                        {item.total_requested_approved}
                      </td>
                      <td className="px-3 py-2 text-center">
                        {item.total_requested_rejected}
                      </td>
                      <td className="px-3 py-2 text-center">
                        {item.total_requested_pending}
                      </td>
                      <td className="px-3 py-2 text-center">
                        {parseInt(item.total_requested_approved) +
                          parseInt(item.total_requested_rejected) +
                          parseInt(item.total_requested_pending)}
                      </td>
                      <td className="py-2">
                        <div className="w-full flex justify-end">
                          <div
                            onClick={() =>
                              this.setState({ viewTransferDetails: item })
                            }
                            className={`${
                              i % 2 !== 0 ? "bg-white" : "bg-gray-100"
                            } group-hover:bg-blue-100 group-hover:text-primary-800 px-2 py-1 cursor-pointer rounded font-bold flex flex-row items-center gap-2 w-max`}
                          >
                            Details
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </DashboardContainer>
        {this.state.confirm_run_algorithm === true && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.props.transferAlgorithm.placing_loading_status === false &&
              this.setState({ confirm_run_algorithm: false })
            }
            backDropClose={true}
            widthSizeClass={ModalSize.medium}
            displayClose={false}
            padding={{
              title: undefined,
              body: undefined,
              footer: undefined,
            }}
          >
            <div className="p-5 py-6">
              <div className="flex flex-col items-center justify-center gap-1">
                <div className="text-accent-900 mt-3">
                  {this.state.success === "" &&
                    this.props.transferAlgorithm.placing_loading_status ===
                      false && (
                      <FaRobot className={`text-8xl ${"animate-bounce"}`} />
                    )}
                </div>
                {this.props.transferAlgorithm.placing_loading_status ===
                false ? (
                  this.state.success !== "" ? (
                    <div className="flex flex-col w-full items-center justify-center p-4">
                      <div>
                        <AiOutlineCheckCircle className="text-9xl text-green-600" />
                      </div>
                      <div className="text-center text-3xl w-full">
                        {this.state.success}
                      </div>
                      <div className="text-center text-xl font-extrabold text-accent-900 w-full">
                        {this.TotalWaiting(
                          this.props.transferAlgorithm.transfers
                        )}{" "}
                        teachers waiting
                      </div>
                      {this.TotalWaiting(
                        this.props.transferAlgorithm.transfers
                      ) === 0 ? (
                        <div className="bg-green-600 rounded px-3 py-2 cursor-pointer text-white hover:bg-green-800">
                          Click to confirm transfer
                        </div>
                      ) : (
                        <div
                          onClick={() => this.RunAlgorithm()}
                          className="bg-accent-900 hover:bg-yellow-900 text-white px-3 py-2 cursor-pointer rounded font-bold flex flex-row items-center gap-2 w-max mt-4"
                        >
                          <div>
                            <VscDebugStart className="text-2xl" />
                          </div>
                          <span>Run again</span>
                        </div>
                      )}
                    </div>
                  ) : (
                    <>
                      <div className="text-3xl font-extrabold animate__animated animate__fadeIn">
                        Start transfer
                      </div>
                      <div className="text-center my-2 mb-5">
                        If you start the process, the robotra will start placing
                        at all districts at time. Please make sure you have
                        stable internet so that the action can not be
                        interrupted
                      </div>
                      <div className="flex flex-row items-center justify-center gap-2">
                        <div
                          onClick={() =>
                            this.props.transferAlgorithm
                              .placing_loading_status === false &&
                            this.setState({ confirm_run_algorithm: false })
                          }
                          className="bg-gray-200 hover:bg-gray-600 text-black hover:text-white px-3 py-2 cursor-pointer rounded font-bold flex flex-row items-center gap-2 w-max"
                        >
                          <div>
                            <MdOutlineKeyboardBackspace className="text-2xl" />
                          </div>
                          <span>Cancel</span>
                        </div>
                        <div
                          onClick={() => this.RunAlgorithm()}
                          className="bg-green-600 hover:bg-green-800 text-white px-3 py-2 cursor-pointer rounded font-bold flex flex-row items-center gap-2 w-max animate__animated animate__fadeIn"
                        >
                          <div>
                            <VscDebugStart className="text-2xl" />
                          </div>
                          <span>Yes, start</span>
                        </div>
                      </div>
                    </>
                  )
                ) : (
                  <div className="flex flex-col items-center justify-center w-full">
                    {/* <Loading /> */}
                    <img src={Robot2} alt="TMIS" className="w-72" />
                    <div className="text-gray-600 text-lg mb-5 text-center -mt-3">
                      Transfer algorithm is running...
                    </div>
                  </div>
                )}
              </div>
            </div>
          </Modal>
        )}
      </Fragment>
    ) : (
      <TransferRequestDetails
        transferDetails={this.state.viewTransferDetails}
        backToApproval={() => {
          this.setState({ viewTransferDetails: null });
        }}
      />
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  transferAlgorithm,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  transferAlgorithm: TransferAlgorithmStore;
} => {
  return {
    auth: auth,
    basic: basic,
    transferAlgorithm: transferAlgorithm,
  };
};

export const TransferApproval = connect(mapStateToProps, {
  FC_Load_All_Transfers,
  Run_Transfer_Algorithm,
})(_TransferApproval);
